import * as React from "react"
import { Component } from 'react';

import './Hello.scss'

type HelloState = {
    message: String
}

interface HelloProps {
    message?: string;
}

export class Hello extends Component<HelloProps, HelloState> {
    render() {
        const { message } = this.props;
        return (
            <p> { message } </p>
        );
    }
}
