import * as React from "react";

import "./App.scss";
import { Hello } from "../components/first/Hello"

class App extends React.Component<Record<string, unknown>, undefined> {
    public render() {
        return (
            <div className="app">
                <h1>Reactjs Playground!</h1>
                <Hello message={ "Hello World"}/>
            </div>
        );
    }
}

export default App;